import { Routes, Route } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { useEffect, lazy, Suspense } from "react";
import { RestrictedRoute } from "./RestrictedRoute";
import { PrivateRoute } from "./PrivateRoute";
import { Layout } from "./Layout";

import { fetchCurrentUser } from '../redux/auth/auth-operations';
import authSelectors from "../redux/auth/auth-selectors";

const HomeView = lazy(() => import('../views/HomeView/HomeView'));
const RegisterView = lazy(() => import('../views/RegisterView/RegisterView'));
const LoginView = lazy(() => import('../views/LoginView/LoginView'));
const ContactsView = lazy(() => import('../views/ContactsView/ContactsView'));

export default function App() {
  const dispatch = useDispatch();
  const isRefreshing = useSelector(authSelectors.selectIsRefreshing);

  useEffect(() => {
    dispatch(fetchCurrentUser());
  }, [dispatch]);

  if (isRefreshing) return 'Fetching user data...';

  return (
    <Suspense fallback={<div>Loading page...</div>}>
      <Routes>
        <Route path="/" element={<Layout />}>
          <Route index element={<HomeView />} />
          <Route
            path="register"
            element={<RestrictedRoute component={RegisterView} redirectTo="/contacts" />}
          />
          <Route
            path="login"
            element={<RestrictedRoute component={LoginView} redirectTo="/contacts" />}
          />
          <Route
            path="contacts"
            element={<PrivateRoute component={ContactsView} redirectTo="/login" />}
          />
        </Route>
      </Routes>
    </Suspense>
  );
}
